h1:target,
h2:target,
h3:target,
h4:target {
  -webkit-animation: slideInDown 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) alternate, fadeIn 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  animation: slideInDown 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) alternate, fadeIn 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
/* TOC */
a.adorn-anchor {
  position: relative;
  text-indent: 0;
  text-decoration: none;
  outline: none;
  opacity: 0;
  transition: opacity 0.3s;
}
:hover > a.adorn-anchor {
  opacity: 0.5;
}
a.adorn-anchor:hover {
  opacity: 1;
}
a.adorn-anchor:before {
  content: "#";
  position: absolute;
  left: -1em;
  color: inherit;
  vertical-align: middle;
}
a.adorn-anchor:hover:before {
  -webkit-transform: scale(1.1);
  -webkit-transform-origin: 50% 50%;
  transform: scale(1.1);
  transform-origin: 50% 50%;
}
.adorn-toolbar {
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transform-origin: 0 0;
  -webkit-animation: fadeIn 0.3s ease;
  animation: fadeIn 0.3s ease;
  background-color: white;
  height: 50px;
  color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
  flex-flow: row;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  left: 0;
  right: 0;
  margin: 0;
  padding: 5px 10px;
  position: fixed;
  top: 0;
  z-index: 1;
  white-space: nowrap;
}
.adorn-toolbar > :last-child {
  text-align: right;
  margin-left: 20px;
}
.adorn-toolbar > :first-child {
  text-align: left;
}
.adorn-toolbar > * {
  flex: 1 0 auto;
  -webkit-flex: 1 0 auto;
  white-space: normal;
  display: inline-block;
}
.adorn-toolbar:after {
  content: "";
  border-bottom: 1px solid #c7c7c7;
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  z-index: -1;
  -webkit-animation: fillWidth 1s linear forwards;
  animation: fillWidth 1s linear forwards;
}
@media all and (max-width: 650px), screen and (max-device-width: 480px) {
  .adorn-toolbar {
    transition: -webkit-transform 1s;
    transition: transform 1s;
  }
  .adorn-toolbar #adorn-edit {
    display: none;
  }
  .adorn-toolbar #adorn-edit + * {
    display: none;
  }
}
.adorn-breadcrumbs {
  white-space: nowrap;
}
.adorn-breadcrumbs > * {
  display: inline-block;
  transition: all 0.3s ease-in;
}
.adorn-breadcrumbs > * > img {
  margin: -5px 10px -5px 0;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  height: 36px;
  vertical-align: middle;
  width: 36px;
  transition: all 0.3s;
}
.adorn-breadcrumbs > * > img:hover {
  -webkit-transform: scale(2.5);
  -webkit-transform-origin: 50% 50%;
  transform: scale(2.5);
  transform-origin: 50% 50%;
  filter: none;
}
.adorn-breadcrumbs > *:hover ~ * {
  transition: all 1s ease-in;
  -webkit-transform: translateX(50px);
  -webkit-transform-origin: 0 0;
  transform: translateX(50px);
  transform-origin: 0 0;
  opacity: 0;
  filter: alpha(opacity=0);
}
.adorn-breadcrumbs > *:last-child {
  text-decoration: none;
  color: #444;
}
.adorn-links {
  white-space: nowrap;
}
.adorn-links > span {
  margin: 0 20px;
}
.adorn-links > span:after {
  content: "\022C5";
}
@media all and (max-width: 650px), screen and (max-device-width: 480px) {
  .adorn-links > span {
    margin: 0 2px;
  }
}
@keyframes slideInDown {
  from {
    transform: translateY(-100px);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fillWidth {
  from {
    width: 0;
    left: 50%;
  }
  to {
    width: 100%;
    left: 0;
  }
}
/* TOC */
.adorn-toc {
  display: inline-block;
  margin-left: 5px;
}
.adorn-toc:before {
  content: '#';
}
/* Sidebar */
.adorn-sidebar {
  background: #eee;
  border-right: 1px solid #c7c7c7;
  height: calc(100% - 50px);
  left: 0;
  overflow: auto;
  padding: 10px;
  position: fixed;
  top: 50px;
  width: 200px;
  -webkit-animation: fadeIn 1s ease;
  animation: fadeIn 1s ease;
}
@media all and (max-width: 1048px) {
  .adorn-sidebar {
    display: none;
  }
}
.adorn-sidebar a {
  text-decoration: none;
  color: inherit;
}
.adorn-sidebar a:hover {
  color: #0C50C7;
}
.adorn-sidebar ul,
.adorn-sidebar li {
  margin: 0;
  padding: 0;
}
.adorn-sidebar > ul {
  margin: 0 -10px;
}
.adorn-sidebar > ul > li > a {
  font-weight: bold;
}
.adorn-sidebar li {
  list-style: none;
  padding: 0 10px;
}
.adorn-sidebar li.selected > a {
  color: #0C50C7;
}
.adorn-sidebar li li {
  font-size: 0.9em;
}
@supports (appearance:none) or (-webkit-appearance:none) or (-moz-appearance:none) {
  .adorn-toc select {
    font: inherit;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-decoration: underline;
    height: 2em;
  }
  .adorn-toc select:not(:focus) {
    border-color: transparent;
    color: inherit;
    background-color: inherit;
  }
  .adorn-toc select::-ms-expand {
    display: none;
  }
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
/* start commented backslash hack \*/
* html .clearfix {
  height: 1%;
}
.clearfix {
  display: block;
}
/* close commented backslash hack */
.adorn-icon-github {
  color: inherit;
  display: inline-block;
  font-style: normal;
  text-decoration: inherit;
}
.adorn-icon-github:after {
  content: "GitHub";
}
