.adorn-speeach-bubble {
  color: #444;
  margin-left: 5px;
  position: relative;
  line-height: 1;
  text-align: center;
  display: inline-block;
  background-color: white;
  border: 1px solid #444;
  color: black;
  border-radius: 15%;
  padding: 5px;
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.adorn-speeach-bubble:before,
.adorn-speeach-bubble:after {
  position: absolute;
  left: -6px;
  bottom: 5px;
  content: " ";
  width: 0;
  height: 0;
  padding: 0;
  border: 5px solid transparent;
  border-width: 3.5px 5px;
  border-right-color: inherit;
  border-left-width: 0;
}
.adorn-speeach-bubble:after {
  border-right-color: white;
  left: -4px;
  border-width: 2.975px 4.25px;
  border-left-width: 0;
  bottom: 5.5px;
}
.adorn-speeach-bubble:empty {
  -webkit-transform: scale(0.01);
  -webkit-transform-origin: 50% 50%;
  transform: scale(0.01);
  transform-origin: 50% 50%;
  opacity: 0;
}
